@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:ital@1&display=swap);
*{
  font-size: 18px;
  font-family: 'Lobster', cursive;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  top: 20%;
  left: 35%;
  position: absolute;
  -webkit-transform: translate(-50,-50);
          transform: translate(-50,-50);
  margin: 0 auto;
  width: 400px;
  text-align: center;
  border: 5px solid black;
  background: black;
  border-radius: 8px;
}

.keypad{
  font-weight: bold;
  font-size: 25px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(70px, auto);
}

input{
  height: 80px;
  width: 383px;
  background-color: blanchedalmond;
  color: black;
  text-align: right;
  font-size: 35px;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 5px;
}

#clear{
  grid-column: 1/4;
  color: brown;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 30px;
}

button{
  text-align: center;
  margin: 7px;
  border-radius: 25px;
  font-weight: bold;
  font-size: 25px;
}

button:hover{
  background-color: goldenrod;
}

.btn{
  background-color: skyblue;
}



